const budgetButton = document.getElementById('budget-button');
const scheduleCall = document.getElementById('schedule-call');

const goToBudgetSection = (clickEvent) => {
  const host = window.location.host;
  const protocol = window.location.protocol;
  const budgetEndpoint = 'budget.html';

  window.location.href = `${protocol}//${host}/${budgetEndpoint}`;
};

const goToContactSection = (clickEvent) => {
  document.getElementById('contact').scrollIntoView({
    behavior: 'smooth',
  });
};

const initButtonActions = () => {
  budgetButton && budgetButton.addEventListener('click', goToBudgetSection);
  scheduleCall && scheduleCall.addEventListener('click', goToContactSection);
};

export { initButtonActions };
