const Glider = require('./vendor/glider.min');

export const applyCarouselSlider = (element) =>
  new Glider(element, {
    slidesToShow: 1,
    slidesToScroll: 1,
    itemWidth: 150,
    draggable: true,
    rewind: true,
    duration: 0.2,
    dots: '#dots',
    arrows: {
      prev: '.glider-prev',
      next: '.glider-next',
    },
    responsive: [
      {
        breakpoint: 520,
        settings: {
          // Set to `auto` and provide item width to adjust to viewport
          slidesToShow: 'auto',
          slidesToScroll: 'auto',
          itemWidth: 150,
          duration: 0.25,
        },
      },
    ],
  });
