const menuLines = document.getElementById('menu-lines');

const showMenu = (clickEvent) => {
  clickEvent.stopPropagation();
  const { parentElement, children } = clickEvent.target;
  parentElement.classList.toggle('is-showing-menu');
  document.body.classList.toggle('is-showing-menu');

  [...children].forEach((element) => {
    element.classList.toggle('is-showing-menu');
  });
};

const hideMenu = () => {
  menuLines.parentElement.classList.remove('is-showing-menu');
  document.body.classList.remove('is-showing-menu');

  [...menuLines.children].forEach((element) => {
    element.classList.remove('is-showing-menu');
  });
};

const listenToCloseMenu = (clickEvent) => {
  clickEvent.stopPropagation();
  const navbar = document.getElementById('nav');
  const clickedElement = clickEvent.target;

  if (
    navbar.className.includes('is-showing-menu') &&
    (!clickedElement.className.includes('nav') ||
      clickedElement.classList.contains('nav__link'))
  ) {
    hideMenu();
  }
};

const initMenu = () => {
  menuLines && menuLines.addEventListener('click', showMenu);
  window.addEventListener('click', listenToCloseMenu);
  window.addEventListener('resize', () => {
    document.documentElement.getBoundingClientRect().width > 768 && hideMenu();
  });
};

export { initMenu };
